import React from "react";
import "./Title.css";

const Title = () => (
  <div className="titleDiv">
    <h1>Space Shuffle</h1>
    <h2>Far out!!</h2>
    <p>Click on an image to earn points but don't click on any more than once!</p>
  </div>
);

export default Title;


